// Update the aspect ratio function
const aspectRatioClass = (width: number, height: number) => {
  const ratio = width / height;
  if (ratio < 0.75) { // Portrait
    return "aspect-[3/4] md:row-span-2 md:col-span-1 h-auto"; // Portrait - spans 2 rows
  } else if (ratio < 0.75) {
    return "aspect-[3/4] row-span-2 md:col-span-1"; // Portrait - spans 2 rows, 1 column
  }
  return "aspect-[4/3] md:row-span-1"; // Standard
};

export default function PhotoGallery() {
  const [currentCategory, setCurrentCategory] = useState<string>('All');
  const [isHeaderVisible, setIsHeaderVisible] = useState(true);
  const [loadedImages, setLoadedImages] = useState<Set<string>>(new Set());

  const filteredPhotos = useMemo(() => 
    currentCategory === 'All' 
      ? photos 
      : photos.filter(photo => photo.category === currentCategory),
    [currentCategory]
  );

  // Update the PhotoSwipe initialization with better responsive settings
  useEffect(() => {
    const lightbox = new PhotoSwipeLightbox({
      gallery: '#photo-gallery',
      children: 'a',
      pswpModule: () => import('photoswipe'),
      padding: { top: 20, bottom: 20, left: 20, right: 20 },
      bgOpacity: 0.9,
      showHideAnimationType: 'fade',
      imageClickAction: 'zoom',
      tapAction: 'zoom',
      preloadFirstSlide: true,
      preload: [2, 4],
      wheelToZoom: true,
      closeOnVerticalDrag: false,
      initialZoomLevel: 'fit',
      secondaryZoomLevel: 1.5,
      maxZoomLevel: 3,
      imageLoadingIndicatorEnabled: true,
      // Enhanced responsive viewport handling
      getViewportSizeFn: (options, pswp) => {
        const width = window.innerWidth;
        const height = window.innerHeight;
        const paddingX = (options.padding?.left || 0) + (options.padding?.right || 0);
        const paddingY = (options.padding?.top || 0) + (options.padding?.bottom || 0);
        
        if (pswp.currSlide?.width && pswp.currSlide?.height) {
          const imageRatio = pswp.currSlide.width / pswp.currSlide.height;
          const viewportRatio = width / height;

          if (imageRatio < 0.75) { // Portrait image
            if (viewportRatio > imageRatio) {
              // Viewport is wider than image ratio
              const targetHeight = height - paddingY;
              const targetWidth = targetHeight * imageRatio;
              return {
                x: Math.min(width - paddingX, targetWidth),
                y: targetHeight
              };
            }
          }
        }
        
        return {
          x: width - paddingX,
          y: height - paddingY
        };
      }
    });

    // Improved responsive handling on image change
    lightbox.on('change', () => {
      if (lightbox.pswp?.currSlide) {
        const slide = lightbox.pswp.currSlide;
        const ratio = slide.width / slide.height;
        const viewportRatio = window.innerWidth / window.innerHeight;

        // Adjust zoom and position based on image orientation
        if (ratio < 0.75) { // Portrait
          if (viewportRatio > ratio) {
            const scale = Math.min(1, window.innerHeight / slide.height);
            slide.zoomTo(scale, {
              x: slide.width / 2,
              y: slide.height / 2
            }, 0, false);
          }
        } else { // Landscape
          const scale = Math.min(1, Math.min(
            window.innerWidth / slide.width,
            window.innerHeight / slide.height
          ));
          slide.zoomTo(scale, {
            x: slide.width / 2,
            y: slide.height / 2
          }, 0, false);
        }
      }
    });

    // Handle resize events
    lightbox.on('resize', () => {
      if (lightbox.pswp?.currSlide) {
        lightbox.pswp.currSlide.updateContentSize(true);
      }
    });

    // Cleanup and event handlers
    lightbox.on('openingAnimationStart', () => {
      document.body.style.overflow = 'hidden';
    });

    lightbox.on('close', () => {
      document.body.style.overflow = 'auto';
    });

    lightbox.init();

    return () => {
      lightbox.destroy();
    };
  }, []);

  // Keep scroll handler
  useEffect(() => {
    const handleScroll = () => {
      const currentScrollY = window.scrollY;
      if (currentScrollY > 100) {
        setIsHeaderVisible(false);
      } else {
        setIsHeaderVisible(true);
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // Modified preload function
  const preloadImage = async (src: string) => {
    if (!loadedImages.has(src)) {
      try {
        const img = document.createElement('img');
        await new Promise((resolve, reject) => {
          img.onload = resolve;
          img.onerror = reject;
          img.src = src;
        });
        setLoadedImages(prev => new Set([...prev, src]));
      } catch (err) {
        console.error('Failed to preload image:', src, err);
      }
    }
  };

  return (
    <>
      {/* Category filter section */}
      <div className={`
        sticky ${isHeaderVisible ? 'top-16' : 'top-0'} 
        z-20 -mx-4 px-4 py-4 
        bg-white/95 backdrop-blur-lg shadow-sm
        transition-all duration-300
      `}>
        <div className="flex flex-nowrap gap-3 overflow-x-auto pb-2 scrollbar-hide">
          {categories.map(category => (
            <button
              key={category}
              onClick={() => setCurrentCategory(category)}
              className={`
                px-4 py-2 rounded-lg transition-all duration-200 whitespace-nowrap
                ${currentCategory === category
                  ? 'bg-gray-900 text-white'
                  : 'bg-gray-100 hover:bg-gray-200 text-gray-700'
                }
              `}
            >
              {category}
            </button>
          ))}
        </div>
      </div>

      {/* Update the grid container className */}
      <div 
        id="photo-gallery" 
        className="grid grid-cols-1 xs:grid-cols-2 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 
          auto-rows-[minmax(200px,_1fr)] grid-flow-dense 
          gap-2 sm:gap-3 md:gap-4 mt-6"
      >
        {filteredPhotos.map((photo, index) => (
          <a
            key={photo.id}
            href={photo.src}
            data-pswp-width={photo.width}
            data-pswp-height={photo.height}
            className={`
              relative cursor-pointer group overflow-hidden rounded-lg bg-gray-100 
              shadow-sm hover:shadow-md transition-all duration-300
              ${aspectRatioClass(photo.width, photo.height)}
            `}
            target="_blank"
            rel="noreferrer"
          >
            <Image
              src={photo.src}
              alt={photo.alt}
              fill
              sizes={`
                (max-width: 480px) 100vw,
                (max-width: 640px) 50vw,
                (max-width: 768px) ${photo.width / photo.height < 0.75 ? '100vw' : '33vw'},
                (max-width: 1024px) ${photo.width / photo.height < 0.75 ? '50vw' : '25vw'},
                ${photo.width / photo.height < 0.75 ? '33vw' : '20vw'}
              `}
              className="object-cover transition-transform duration-300 group-hover:scale-105"
              loading={index < 8 ? "eager" : "lazy"}
              priority={index < 8}
              quality={100} // Maximum quality for all images
              placeholder="blur"
              blurDataURL={`data:image/svg+xml;base64,${Buffer.from(
                `<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 ${photo.width} ${photo.height}">
                  <rect width="100%" height="100%" fill="#F3F4F6"/>
                </svg>`
              ).toString('base64')}`}
              onLoadingComplete={() => preloadImage(photo.src)}
            />
            <div className="absolute inset-0 bg-gradient-to-t from-black/50 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300 flex items-end justify-start p-4">
              <div className="text-white">
                <h3 className="text-sm font-medium">{photo.alt}</h3>
                <p className="text-xs opacity-75">{photo.category}</p>
              </div>
            </div>
          </a>
        ))}
      </div>
    </>
  );
}